rina\";\n  src:url(\"../fonts/LondrinaSolid-Black.woff2\") format(\"woff2\"),url(\"../fonts/LondrinaSolid-Black.woff\") format(\"woff\");\n  font-style:normal;font-weight:bold;\n}\n\n@import url('https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;600;700&display=swap');\n\n@mixin transform($transforms) {\n  -moz-transform: $transforms;\n  -o-transform: $transforms;\n  -ms-transform: $transforms;\n  -webkit-transform: $transforms;\n  transform: $transforms;\n}\n\n@mixin transition($transition) {\n  -moz-transition: $transition $animation-duration ease-in-out;\n  -o-transition: $transition $animation-duration ease-in-out;\n  -ms-transform: $transition $animation-duration ease-in-out;\n  -webkit-transform: $transition $animation-duration ease-in-out;\n  transition: $transition $animation-duration ease-in-out;\n}\n\n$animation-duration: 0.2s;\n\n@mixin aspect-ratio($width, $height) {\n  position: relative;\n  &:before {\n    display: block;\n    content: \"\";\n    width: 100%;\n    padding-top: calc(($height / $width) * 100%);\n  }\n  > div {\n    position: absolute!important;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n  }\n}\n\n* {\n  box-sizing: border-box;\n}\n\n$small: .65rem;\n$initial: 16px;\n$medium: 1.777rem;\n$large: 2.369rem;\n$larger: 4.209rem;\n$xl:5.61rem;\n\nhtml {\n  overflow-x: hidden;\n  display: -webkit-box;\n\n  box-sizing: border-box;\n    font-size: $initial;\n    font-family: \"Work Sans\",sans-serif;\n    word-spacing: 1px;\n    -ms-text-size-adjust: 100%;\n    -webkit-text-size-adjust: 100%;\n    -moz-osx-font-smoothing: grayscale;\n    -webkit-font-smoothing: antialiased;\n}\n\nbody {\n  position: relative;\n  float: left;\n  width: 100%;\n  margin: 0;\n  font-family: 'Work Sans', sans-serif;\n  font-weight: 400;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  padding: 0;\n  background: #fff;\n  color: #9ca3af;\n  font-size: 16px;\n  overflow-x: hidden;\n\n}\n\nh1,h2,h3 {\n  font-family: \"Londrina\";\n  font-weight:bold;\n  color: #333;\n}\n\nh1 {\n  font-size: $xl;\n}\n\nh2 {\n  font-size: $larger;\n}\n\nh3 {\n  font-size: $large;\n}\n\n.u {\n  color: #8ec1de;\n  text-decoration: underline;\n  text-transform: uppercase;\n}\n\n.l {\n  text-transform: lowercase;\n}\n\n\na {\n  text-decoration: none;\n  color: #1a1b1f;\n  @include transition(all);\n  &:hover {\n    color: #6DD0F7;\n  }\n}\n\np {\n      line-height: calc(16px * 1.5);\n      margin: 0 0 0.5rem;\n      &:last-child {\n        margin-bottom: 0;\n      }\n}\n\nb {\n  color: #707070;\n}\n\nheader {\n  position: relative;\ndisplay: block;\nmargin: 0 auto;\nwidth: 100%;\nmax-width: 1280px;\nheight: 4.75rem;\nline-height: 4.75rem;\npadding: 0 40px;\ndisplay: grid;\ngrid-template-columns: 1fr 1fr 1fr;\n  .logo{\n    img {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      @include transform(translate(-50%,-50%));\n      height: 3rem;\n    }\n  }\n  .social {\n    position: absolute;\n    right: 40px;\n    height: 4.75rem;\n    a {\n      display: inline-block;\n      margin-left: 2rem;\n      width: 30px;\n\n    svg {\n      height: 30px;\n      width: 30px;\n      margin: calc((4.75rem - 30px) / 2 ) 0;\n      @include transition(all);\n      .fill {\n        fill:#000;\n        @include transition(all);\n      }\n    }\n    &:hover {\n      svg {\n        @include transform(scale(1.2,1.2));\n        .fill {\n          fill:#6DD0F7;\n        }\n      }\n    }\n  }\n  }\n}\n\n.treasury {\n  left: 40px;\n  position: absolute;\n  top: 50%;\n  @include transform(translateY(-50%));\n  line-height: 20px;\n  font-weight: 500;\n  a {\n    text-decoration: none;\n    color: inherit;\n  }\n    span {\n      font-size: $small;\n      text-transform: uppercase;\n    }\n    #balance {\n      display: block;\n      color: #1a1b1f;\n      font-weight: bold;\n      img {\n        height: 20px;\n        position: relative;\n        float: left;\n        margin-right: .5rem;\n      }\n    }\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n#mint {\n  @include aspect-ratio(15,7);\n  background-color: #7bcce8;\n  margin-bottom: 6.25rem;\n  width: calc(100% - 80px);\n  border-radius: 24px;\n  max-width: 1200px;\n  background-image: url('https://uma.sebastien.wtf/img/bg.jpg');\n  background-size: cover;\n    background-position: bottom;\n  a {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    @include transform(translate(-50%,-50%));\n    height:100%;\n    line-height: 3.75rem;\n    padding: 0 1.5625rem;\n    text-transform: uppercase;\n    text-decoration: none;\n    @include transition(all);\n\n    font-family: \"Londrina\";\n    font-weight:bold;\n    color: #000;\n    z-index: 1;\n  font-size: $xl;\n  span {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    @include transform(translate(-50%,-50%));\n      @include transition(all);\n  }\n  img {\n      position: absolute;\n      height: 100%;\n      width: auto;\n      top: 0;\n      left: 50%;\n      transform: translate(-50%, 0);\n      transform-origin: center center;\n      @include transition(all);\n    }\n\n    &:hover {\n      span {\n      @include transform(translate(-50%,-50%) scale(1.05, 1.05));\n    }\n        img {\n          @include transform(translate(-50%,0) scale(1.1, 1.1));\n        }\n      // color: #fff;\n      // background: #1a1b1f;\n    }\n  }\n}\n\n.aspiring {\n  font-size: $large;\n  text-align: center;\n  font-family: \"Londrina\";\n  font-weight:bold;\n  color: #333;\n  h3 {\n    display: block;\n    width: 100%;\n\n  }\n  span {\n    font-size: $larger;\n    color: #000;\n  }\n}\n\nsection {\n  display: block;\n  margin: 0 auto;\n  width: 100%;\n  max-width: 1280px;\n  padding: 0 40px;\n}\n\nsection.content {\n  margin-bottom: 6.25rem;\n  border-radius: 24px;\n  box-shadow: 0 2.125rem 5.25rem rgb(211 222 232 / 21%);\n  .container {\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n    padding: 0 40px;\n    grid-column-gap: 2rem;\n    .col {\n      position: relative;\n      display: block;\n      &:nth-child(1) {\n        .colContent:before {\n          background: linear-gradient(180deg,#47A9D0,hsla(0,0%,100%,0));\n        }\n      }\n      &:nth-child(2) {\n        .colContent:before {\n          background: linear-gradient(180deg,#47a9d0,hsla(0,0%,100%,0));\n        }\n      }\n      &:nth-child(3) {\n        .colContent:before {\n          background: linear-gradient(180deg,#47A9D0,hsla(0,0%,100%,0));\n        }\n      }\n      .colHero {\n        position: relative;\n        text-align: center;\n        font-family:\"Londrina\";\n        letter-spacing: .05rem;\n        color: #1a1b1f;\n        h3 {\n          font-size: $larger;\n          line-height: calc($larger * 1.5);\n          margin: 0;\n          .s {\n            font-size: $large;\n            vertical-align: top;\n          }\n        }\n      }\n      .colContent {\n        position: relative;\n        text-align: center;\n      }\n        // line-height: 130%;\n        // letter-spacing: -.04em;\n        font-weight: 500;\n        a {\n          font-weight: 600;\n        }\n    }\n  }\n}\n\nsection.propHouse {\n  text-align: center;\n      margin-bottom: 6.25rem;\n      a {\n        display: inline-block;\n        &:hover {\n              color: initial;\n          @include transform(scale(1.05, 1.05));\n        }\n      }\n      h1 {\n        margin-bottom: 1.04rem;\n        .status {\n          // background-color: #6DD0F7;\n          // background: linear-gradient(90deg,#6DD0F7, #CECECE);\n          // background: linear-gradient(var(--gradient-orientation,90deg),, #CECECE);\n          // -webkit-background-clip: text;\n          // background-clip: text;\n          // -webkit-text-fill-color: transparent;\n          color: #6DD0F7;\n          display: inline-block;\n        }\n      }\n      .deadline {\n        text-transform: uppercase;\n        font-weight: 500;\n        letter-spacing: -.02rem;\n      }\n  h2 {\n    // text-align: right;\n   text-align: center;\n   span {\n     display: block;\n     font-size: $large;\n     color: #707070;\n   }\n  }\n  img {\n    width: 140px;\n    @include transition(all);\n  }\n  a:hover {\n    img {\n      @include transform(scale(1.05,1.05))\n    }\n  }\n}\n\nsection.faq {\n      padding-bottom: 6.25rem;\n      background-color: #edf0f2;\n      margin: 0;\n      max-width: unset;\n      .content {\n        max-width: 1280px;\n        padding: 0 40px;\n        display: block;\n        margin: 0 auto;\n        div {\n          max-width: 840px;\n          margin: 0 auto;\n        }\n      }\n      h2 {\n        position: relative;\n        width: 100%;\n        text-align: center;\n        display: inline-block;\n        margin-bottom: 0;\n        margin-bottom: 1.04rem;\n      }\n  h3 {\n    color: #707070;\n    margin-bottom: 1rem;\n  }\n}\n\nfooter {\n  position: relative;\n  float: left;\n  width: 100%;\n  text-align: center;\n  font-weight: 600;\n  padding: 6.25rem 40px 40px;\n  background: #47A9D0;\ncolor: #fff;\n  ul {\n    list-style: none;\n    margin: 0 0 40px;\n    padding: 0;\n    li {\n      display: inline-block;\n          margin: 0 2rem;\n          text-transform: uppercase;\n\n      a {\n        color: #fff;\n        font-weight: 500;\n        display: block;\n        &:hover {\n          @include transform(scale(1.1,1.1));\n          color: inherit;\n        }\n      }\n    }\n  }\n  .noCopyright {\n    font-size: 0.7rem;\n  }\n}\n\nspan.spin {\n  position: relative;\n  overflow: hidden;\n  width: 100%;\n  height: calc($larger * 1.5);\n  text-align: center;\n  display: block;\n}\n\nspan.item, span.item-hu{\n  position: absolute;\n  transform-origin: 50% 70%;\n  left: 0;\n  text-transform: uppercase;\n  width: 100%;\n}\n\n\nspan.item:nth-child(1) {\n  animation: flipUp 15s cubic-bezier(0.68, -0.55, 0.26, 1.55) 0s both infinite;\n}\nspan.item:nth-child(2) {\n  animation: flipUp 15s cubic-bezier(0.68, -0.55, 0.26, 1.55) 3s both infinite;\n}\nspan.item:nth-child(3) {\n  animation: flipUp 15s cubic-bezier(0.68, -0.55, 0.26, 1.55) 6s both infinite;\n}\nspan.item:nth-child(4) {\n  animation: flipUp 15s cubic-bezier(0.68, -0.55, 0.26, 1.55) 9s both infinite;\n}\nspan.item:nth-child(5) {\n  animation: flipUp 15s cubic-bezier(0.68, -0.55, 0.26, 1.55) 12s both infinite;\n}\n\n\n\nspan.item-hu:nth-child(1) {\n  animation: flipUpHero 8s cubic-bezier(0.68, -0.55, 0.26, 1.55) 0s both infinite;\n}\nspan.item-hu:nth-child(2) {\n  animation: flipUpHero 8s cubic-bezier(0.68, -0.55, 0.26, 1.55) 4s both infinite;\n}\n\n\n/* ANIMATION */\n\n@keyframes flipUp {\n  0% {\n   opacity: 0;\n   margin-top: -40px;\n  }\n  10%{\n    opacity: 1;\n    margin-top: 0;\n    transform: rotateX(0deg);\n  }\n  30%,100% {\n    opacity: 0;\n    margin-top: 0;\n    transform: rotateX(90deg);\n  }\n}\n\n@keyframes flipUpHero {\n  0% {\n   opacity: 0;\n   margin-top: -40px;\n  }\n  20%{\n    opacity: 1;\n    margin-top: 0;\n    transform: rotateX(0deg);\n  }\n  80%,100% {\n    opacity: 0;\n    margin-top: 0;\n    transform: rotateX(90deg);\n  }\n}\n\n\n\n.parallax-wrap {\n  position: absolute;\n  top: 0;\n  pointer-events: none;\n  width: 100%;\n  height: 100vh;\n  overflow: hidden;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.parallax-wrap h2 {\n  position: relative;\n  font-size: 100px;\n  color: white;\n  z-index: 2;\n  text-align: center;\n}\n.parallax-wrap span {\n  position: absolute;\n  height: 20px;\n  width: 20px;\n  border-radius: 100%;\n}\n.parallax-wrap span:nth-child(1) {\n  top: 70%;\n  left: 70%;\n  background: blue;\n  z-index: 3;\n}\n.parallax-wrap span:nth-child(2) {\n  top: 60%;\n  left: 80%;\n  background: yellow;\n  z-index: 3;\n}\n.parallax-wrap span:nth-child(3) {\n  top: 40%;\n  left: 60%;\n  background: green;\n  z-index: 3;\n}\n.parallax-wrap span:nth-child(4) {\n  top: 70%;\n  left: 40%;\n  background: red;\n  z-index: 3;\n}\n.parallax-wrap span:nth-child(5) {\n  top: 40%;\n  left: 30%;\n  background: purple;\n  z-index: 3;\n}\n\n\n\n@media screen and (max-width: 640px) {\n  header {\n    padding: 0 15px;\n    .treasury {\n      left: 15px;\n    }\n    .social {\n      right:15px;\n    }\n  }\n\n  section, #mint {\n    margin: 0 15px 40px;\npadding: 0 15px;\nwidth: calc(100% - 30px);\n  }\n\n  section.content {\n    .container {\n      padding: 0;\n      grid-template-columns: 1fr;\n      grid-row-gap: 3rem;\n    }\n  }\n\n  section.faq {\n    width: 100%;\n    .content {\n      padding:0;\n    }\n  }\n}\n"],"sourceRoot":""}]);
